import React, { useState, useEffect, useRef } from 'react';
import 'aframe';
import 'aframe-react';

const MyVRScene = () => {
  const [isMuted, setIsMuted] = useState(true);
  const videoRef = useRef();
  const videoURL = process.env.PUBLIC_URL + '/' +'360-sea-mountain.mp4';

  useEffect(() => {
    const playVideo = async () => {
      if (videoRef.current) {
        try {
          await videoRef.current.play();
        } catch (error) {
          console.error('Autoplay failed:', error);
          // Handle autoplay failure here
        }
      }
    };

    playVideo();
  }, []);

  const toggleMute = () => {
    if (videoRef.current) {
      videoRef.current.muted = !videoRef.current.muted;
      setIsMuted(videoRef.current.muted);
    }
  };

  return (
    <a-scene>
      <a-assets>
        <video
          id="myVideo"
          src={videoURL}
          loop
          autoplay
          muted={isMuted}
          playsinline
          crossorigin="anonymous"
          ref={videoRef}
          style={{ display: 'none' }}
        ></video>
      </a-assets>

      {/* Left eye */}
      <a-sphere
        position="-3 2 -5"
        radius="3"
        material={`src: #myVideo; shader: flat; side: double;`}
        rotation="0 180 0"
      ></a-sphere>

      {/* Right eye */}
      <a-sphere
        position="3 2 -5"
        radius="3"
        material={`src: #myVideo; shader: flat; side: double;`}
        rotation="0 180 0"
      ></a-sphere>

      {/* Camera for VR headset */}
      <a-camera position="0 2 0">
        <a-cursor></a-cursor>
      </a-camera>

      {/* Mute/Unmute button */}
      <button onClick={toggleMute}>
        {isMuted ? 'Unmute' : 'Mute'}
      </button>
    </a-scene>
  );
};

export default MyVRScene;
